.list {
    width: 100%;
    margin-top: 10px;
    .listTitle {
        color: #fff;
        font-size: 20px;
        font-weight: 500;
        margin-left: 50px;
    }

    .wrapper {
        position: relative;
        .sliderArrow {
            width: 50px;
            height: 100%;
            background-color: rgb(22, 22, 22, 0.5);
            color: #fff;
            position: absolute;
            z-index: 99;
            top: 0;
            background-origin: padding-box;
            margin: auto;
            cursor: pointer;
            &.left {
                left: 0;
            }
            &.right {
                right: 0;
            }
        }
        .container {
            margin-left: 50px;
            margin-top: 10px;
            transform: translateX(0px);
            display: flex;
            width: max-content;
            transition: all .4s ease;
        }
    }
}